/* ===== Background Layer ===== */
.container-back-img {
  background-image: url('./../public/images/liveserver-chat-app-background.jpg'); /* choose a tech / abstract / chat-themed image */
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 100%;
  height: 100vh;
  position: fixed;
  z-index: -2;
}


  /* blue-turquoise gradient overlay */
