  background: linear-gradient(135deg, rgba(10, 125, 200, 0.85), rgba(0, 75, 150, 0.85));
  width: 100%;
  height: 100vh;
  position: fixed;
  z-index: -1;
}
  */

  .container-back-color {
  /* blue-turquoise gradient overlay */

  background: linear-gradient(135deg, rgba(50, 8, 239, 0.85), rgba(158, 6, 6, 0.85));
  width: 100%;
  height: 100vh;
  position: fixed;
  z-index: -1;
}

/* ===== Centering Container ===== */
.container {
  height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 15px;
}

/* ===== Login Card ===== */
.card {
  background-color: #ffffff;
  border-radius: 16px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.2);
  width: 380px;
  padding: 40px 35px;
  transition: all 0.3s ease;
}
/*
.card:hover {
  transform: translateY(-4px);
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.25);
}
  */

/* ===== Title Section ===== */
.card_title {
  text-align: center;
  margin-bottom: 25px;
}

.card_title h1 {
  font-size: 28px;
  font-weight: 700;
  color: #0a3d66; /* dark blue */
  letter-spacing: 0.5px;
}

/* ===== Input Fields ===== */
.form input {
  width: 100%;
  background-color: #f0f4f8;
  border: 1px solid #cfdce6;
  outline: none;
  padding: 14px 18px;
  border-radius: 8px;
  margin-bottom: 15px;
  font-size: 15px;
  transition: all 0.3s ease;
}

.form input:focus {
  border-color: #1089ff;
  box-shadow: 0 0 4px rgba(16, 137, 255, 0.4);
  background-color: #fff;
}

/* ===== Login Button ===== */
.form button {
  background: linear-gradient(90deg, #1089ff, #0a6fcc);
  color: #fff;
  font-size: 16px;
  font-weight: 600;
  outline: none;
  border: none;
  border-radius: 8px;
  padding: 12px 15px;
  width: 100%;
  cursor: pointer;
  margin-top: 10px;
  transition: all 0.3s ease;
}

.form button:hover {
  background: linear-gradient(90deg, #0a6fcc, #085bbe);
  transform: translateY(-2px);
}

/* ===== Terms / Footer ===== */
.card_terms {
  display: flex;
  align-items: center;
  padding: 10px 0;
}

.card_terms span {
  margin-left: 8px;
  font-size: 13px;
  color: #555;
}

.card a {
  color: #1089ff;
  font-weight: 600;
  text-decoration: none;
  transition: color 0.3s ease;
}

.card a:hover {
  color: #0a6fcc;
  text-decoration: underline;
}

/* ===== Responsive Design ===== */
@media (max-width: 450px) {
  .card {
    width: 100%;
    padding: 30px 20px;
  }

  .card_title h1 {
    font-size: 24px;
  }
}


/********************************************************************
***************************CSS FOR LOADIER**************************
********************************************************************/

.loader-overlay{
  position: fixed; /* Sit on top of the page content */
  width: 100%; /* Full width (cover the whole page) */
  height: 100%; /* Full height (cover the whole page) */
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(231,76,60,0.7); /* Black background with opacity */
  z-index: 2; /* Specify a stack order in case you're using a different order for other elements */
  align-items: center;
  display: flex;
  justify-content: center;
}
 
.loader-spinner,
.loader-spinner:after {
  box-sizing: border-box;
}
.loader-spinner {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.loader-spinner:after {
  content: " ";
  display: block;
  border-radius: 50%;
  width: 0;
  height: 0;
  margin: 8px;
  box-sizing: border-box;
  border: 32px solid white;
  border-color: white transparent white transparent;
  animation: loader-spinner 1.2s infinite;
}
@keyframes loader-spinner {
  0% {
    transform: rotate(0);
    animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
  }
  50% {
    transform: rotate(900deg);
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }
  100% {
    transform: rotate(1800deg);
  }
}

