  cursor: pointer;
  background-color: #e74c3c;
}

.filter-user-display {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: nowrap;
  gap: 10px;
}

.user-profile-image,
.user-default-avatar, .user-selected-avatar {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  
  font-size: 22px;
  font-weight: bold;
  text-align: center;
  line-height: 50px;
  flex-shrink: 0;
}
.user-dafault-avatar{
  background-color: #e74c3c;
  color: white;

}
.user-selected-avatar{
  background-color: white;
  color: #e74c3c;
}

.filter-user-details {
  flex-grow: 1;
  overflow: hidden;
  min-width: 0;
}

.user-display-name {
  font-size: 16px;
  font-weight: bold;
  margin: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.user-display-email {
  font-size: 12px;
  margin: 0;
  white-space: normal;
  overflow: visible;
  text-overflow: unset;
  color: #666;
}

.user-start-chat {
  flex-shrink: 0;
}

.user-start-chat-btn {
  padding: 8px 12px;
  border: none;
  border-radius: 3px;
  background-color: #e74c3c;
  color: white;
  white-space: nowrap;
}

